import { API_BASE_URL } from "@/config";
import axios from "axios";
import Vue from "vue";
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartProductsData: [],
        orderInfo: null,
    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        addProductToCart(state, { productId, amount }) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if (item) {
                item.amount += amount;
            } else {
                state.cartProducts.push({
                    productId,
                    amount
                });
            }
        },
        updateCartProductAmount(state, { productId, amount }) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if (item) {
                item.amount = amount;
            }
        },
        deleteProductFromCart(state, { productId }) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
        },
        updateUserAccessKey(state, accesskey) {
            state.userAccessKey = accesskey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            });
        }
    },
    getters: {
        cartDetailProducts(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                }
            });
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        },
        cartTotalAmount(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (item.amount) + acc, 0);
        }
    },
    actions: {
        loadOrderInfo(context, orderId) {
            return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
            .then(res => {
                context.commit('updateOrderInfo', res.data);
            })
        },
        async loadCart(context) {
            const res = await axios.get(API_BASE_URL + '/api/baskets', {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            });
            if (!context.state.userAccessKey) {
                localStorage.setItem('userAccessKey', res.data.user.accessKey);
                context.commit('updateUserAccessKey', res.data.user.accessKey);
            }
            context.commit('updateCartProductsData', res.data.items);
            context.commit('syncCartProducts');
        },
        async addProductToCart(context, { productId, amount }) {
            await (new Promise(resolve => setTimeout(resolve, 2000)));
            const res = await axios.post(API_BASE_URL + '/api/baskets/products', {
                productId: productId,
                quantity: amount,
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            });
            context.commit('updateCartProductsData', res.data.items);
            context.commit('syncCartProducts');
        },
        updateCartProductAmount(context, { productId, amount }) {
            context.commit('updateCartProductAmount', { productId, amount });

            return axios.put(API_BASE_URL + '/api/baskets/products', {
                productId: productId,
                quantity: amount,
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
                .then(res => {
                    context.commit('updateCartProductsData', res.data.items);
                })
                .catch(() => {
                    context.commit('syncCartProducts');
                })
        },
        deleteProductFromCart(context, productId) {
            return axios.delete(API_BASE_URL + '/api/baskets/products', {
                params: {
                    userAccessKey: context.state.userAccessKey
                },
                data: { productId: productId }
            })
                .then(res => {
                    context.commit('updateCartProductsData', res.data.items);
                    context.commit('syncCartProducts');
                });
        }
    }
});