<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click.prevent="amountDecrement">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="currentAmount" @input="$emit('updateAmount', currentAmount)">

        <button type="button" aria-label="Добавить один товар" @click.prevent="amountIncrement">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
    props: ['amount'],
    model: {
        prop: 'amount',
        event: 'updateAmount'
    },
    data() {
        return {
            currentAmount: this.amount
        }
    },
    methods: {
        amountDecrement() {
            this.currentAmount--;
            if (this.currentAmount < 1) {
                this.currentAmount = 1;
            }
            this.$emit('updateAmount', this.currentAmount);
        },
        amountIncrement() {
            this.currentAmount++;
            this.$emit('updateAmount', this.currentAmount);
        }
    }
}
</script>