<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img :src="item.product.image" width="120" height="120" alt="item.product.title ">
        </div>
        <h3 class="product__title">
            {{ item.product.title }}
        </h3>
        <!-- <p class="product__info product__info--color">
                                Цвет:
                                <span>
                                    <i style="background-color: #FF6B00"></i>
                                    Оранжевый
                                </span>
                            </p> -->
        <span class="product__code">
            Артикул: {{ item.productId }}
        </span>

        <div class="product__counter form__counter">
            <button type="button" aria-label="Убрать один товар" @click.prevent="amount - 1 <= 0 ? $store.dispatch('deleteProductFromCart', item.productId) : amount--">
                <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                </svg>
            </button>

            <input type="text" v-model.number="amount">

            <button type="button" aria-label="Добавить один товар" @click.prevent="amount++">
                <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                </svg>
            </button>
        </div>

        <b class="product__price">
            {{ (item.amount * item.product.price) | numberFormat }} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="$store.dispatch('deleteProductFromCart', item.productId)">
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
// import { mapMutations } from 'vuex';

export default {
    props: ['item'],
    filters: {
        numberFormat
    },
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
            }
        }
    },
    // methods: {
    //     ...mapMutations({ deleteProduct: 'deleteProductFromCart' }),
    // }
}
</script>