<template>
    <main class="content container">
        <div class="content__top content__top--catalog">
            <h1 class="content__title">
                Каталог
            </h1>
            <span class="content__info">
                152 товара
            </span>
        </div>

        <div class="content__catalog">
            <ProductFilter :category-id.sync="filterCategoryId" :price-from.sync="filterPriceFrom"
                :price-to.sync="filterPriceTo" :color.sync="filterColor" />
            <section class="catalog">
                <div v-if="productsLoading">
                    Загрузка...
                </div>
                <div v-else-if="productsLoadingError">
                    Произошла ошибка при загрузке товаров <br>
                    <button @click.prevent="loadProducts">
                        Попробовать ещё раз
                    </button>
                </div>
                <ProductList v-else :products="products" :color.sync="filterColor" />
                <BasePagination v-model="page" :per-page="productsPerPage" :count="countProducts" />
            </section>
        </div>
    </main>
</template>

<script>
import ProductList from '../components/ProductList'
import BasePagination from '../components/BasePagination.vue'
import ProductFilter from '../components/ProductFilter.vue'
import axios from 'axios'
import { API_BASE_URL } from '@/config';

export default {
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filterColor: 0,
            page: 1,
            productsPerPage: 3,
            productsData: null,
            productsLoading: false,
            productsLoadingError: false
        }
    },
    computed: {
        products() {
            return this.productsData ? this.productsData.items.map(product => {
                return {
                    ...product,
                    image: product.image.file.url
                }
            }) : [];
        },
        countProducts() {
            return this.productsData ? this.productsData.pagination.total : 0;
        }
    },
    components: {
        ProductList,
        BasePagination,
        ProductFilter
    },
    methods: {
        loadProducts() {
            this.productsLoading = true;
            this.productsLoadingError = false;
            clearTimeout(this.loadTimer);
            this.loadTimer = setTimeout(() => {
                axios.get(API_BASE_URL + '/api/products', {
                    params: {
                        page: this.page,
                        limit: this.productsPerPage,
                        categoryId: this.filterCategoryId,
                        colorId: this.filterColor,
                        minPrice: this.filterPriceFrom,
                        maxPrice: this.filterPriceTo
                    }
                })
                    .then(res => this.productsData = res.data)
                    .catch(() => this.productsLoadingError = true)
                    .then(() => this.productsLoading = false);
            }, 1000);
        }
    },
    watch: {
        page() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
        filterColor() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        }
    },
    created() {
        this.loadProducts();
    }
}
</script>