<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
                        Каталог
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
                        Корзина
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        Оформление заказа
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                Заказ оформлен <span>№ {{ orderInfo.id }}</span>
            </h1>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST">
                <div class="cart__field">
                    <p class="cart__message">
                        Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
                        Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
                    </p>

                    <ul class="dictionary">
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Получатель
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.name }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Адрес доставки
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.address }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Телефон
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.phone }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Email
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.email }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Способ оплаты
                            </span>
                            <span class="dictionary__value">
                                картой при получении
                            </span>
                        </li>
                    </ul>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <li class="cart__order" v-for="item in orderInfo.products" :key="item.product.id">
                            <h3>{{ item.product.title }}</h3>
                            <b>{{ item.product.price | numberFormat }} ₽</b>
                            <span>Артикул: {{ item.product.id }}</span>
                        </li>
                    </ul>

                    <div class="cart__total">
                        <p>Доставка: <b>500 ₽</b></p>
                        <p>Итого: <b>{{ orderInfo.products.length }}</b> {{ {
                            number: orderInfo.products.length, one: 'товар', two: 'товара',
                            five: 'товаров'
                        } | getNoun }} на сумму <b>{{ orderInfo.totalPrice | numberFormat }} ₽</b></p>
                    </div>
                </div>
            </form>
        </section>
    </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import getNoun from '@/helpers/getNoun';
export default {
    data() {
        return {
            orderData: null
        }
    },
    created() {
        if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
            this.orderData = this.$store.state.orderInfo;
            return;
        }
        clearTimeout(this.loadingTimer);
        this.$store.dispatch('loadOrderInfo', this.$route.params.id);
        this.loadingTimer = setTimeout(() => {
            this.orderData = this.$store.state.orderInfo;
        }, 100);
    },
    computed: {
        orderInfo() {
            if (this.orderData) {
                return {
                    ...this.orderData,
                    products: this.orderData.basket.items
                }
            } else {
                return {};
            }
        }
    },
    filters: {
        numberFormat,
        getNoun
    }
}
</script>