<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
                        Каталог
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
                        Корзина
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{ name: 'order' }">
                        Оформление заказа
                    </router-link>
                </li>
            </ul>

            <h1 class="content__title">
                Корзина
            </h1>
            <span class="content__info">
                {{ products.length }} {{ { number: products.length, one: 'товар', two: 'товара', five: 'товаров' } | getNoun
                }}
            </span>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
                <div class="cart__field">
                    <div class="cart__data">
                        <BaseFormText title="ФИО" v-model="formData.name" placeholder="Введите ваше полное имя"
                            :error="formError.name" />
                        <BaseFormText title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес"
                            v-model="formData.address" />
                        <BaseFormText title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон"
                            v-model="formData.phone" type="tel" />
                        <BaseFormText title="Email" :error="formError.email" placeholder="Введите ваш Email"
                            v-model="formData.email" type="email" />

                        <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment"
                            placeholder="Ваши пожелания" v-model="formData.comment" />
                    </div>

                    <div class="cart__options">
                        <h3 class="cart__title">Доставка</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                                    <span class="options__value">
                                        Самовывоз <b>бесплатно</b>
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                                    <span class="options__value">
                                        Курьером <b>500 ₽</b>
                                    </span>
                                </label>
                            </li>
                        </ul>

                        <h3 class="cart__title">Оплата</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="card">
                                    <span class="options__value">
                                        Картой при получении
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                                    <span class="options__value">
                                        Наличными при получении
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <li class="cart__order" v-for="item in products" :key="item.productId">
                            <h3>{{ item.product.title }}</h3>
                            <b>{{ item.product.price | numberFormat }} ₽</b>
                            <span>Артикул: {{ item.productId }}</span>
                        </li>
                    </ul>

                    <div class="cart__total">
                        <p>Доставка: <b>500 ₽</b></p>
                        <p>Итого: <b>{{ products.length }}</b> {{ {
                            number: products.length, one: 'товар', two: 'товара',
                            five: 'товаров'
                        } | getNoun }} на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
                    </div>

                    <button class="cart__button button button--primery" type="submit">
                        Оформить заказ
                    </button>
                </div>
                <div class="cart__error form__error-block" v-if="formErrorMessage">
                    <h4>Заявка не отправлена!</h4>
                    <p>
                        {{ formErrorMessage }}
                    </p>
                </div>
                <div v-show="orderLoading">Оформляем заказ...</div>
                <div v-show="orderComplete">Заказ оформлен!</div>
            </form>
        </section>
    </main>
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import getNoun from '@/helpers/getNoun';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
    data() {
        return {
            formData: {},
            formError: {},
            formErrorMessage: '',
            orderLoading: false,
            orderComplete: false
        }
    },
    computed: {
        ...mapGetters({
            products: 'cartDetailProducts',
            totalPrice: 'cartTotalPrice',
            totalAmount: 'cartTotalAmount'
        })
    },
    filters: {
        numberFormat,
        getNoun
    },
    components: {
        BaseFormText,
        BaseFormTextarea
    },
    methods: {
        order() {
            this.orderLoading = true;
            this.formError = {};
            this.formErrorMessage = '';

            return (new Promise(resolve => setTimeout(resolve, 2000)))
                .then(() => {
                    axios.post(API_BASE_URL + '/api/orders', {
                        ...this.formData
                    }, {
                        params: {
                            userAccessKey: this.$store.state.userAccessKey
                        }
                    })
                        .then(res => {
                            this.$store.commit('updateOrderInfo', res.data);
                            this.$store.commit('resetCart');
                            this.orderLoading = false;
                            this.orderComplete = true;
                            this.$router.push({ name: 'orderInfo', params: { id: res.data.id } });
                        })
                        .catch(error => {
                            this.orderLoading = false;
                            this.formError = error.response.data.error.request || {};
                            this.formErrorMessage = error.response.data.error.message;
                        })
                })
        }
    }
}
</script>